import React, { useState, useEffect } from 'react';
import './home/Home.css';
import myPhoto from './img/myPhoto.png';
import myPhoto3 from './img/myPhoto3.png';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebook } from '@fortawesome/free-brands-svg-icons';
import { faYoutube } from '@fortawesome/free-brands-svg-icons';
import { faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faGithub } from '@fortawesome/free-brands-svg-icons';

import Navigationcode from './navigationcode';

const Navbar = React.lazy(()=>import('./Navbar'));
const Copyright = React.lazy(()=>import('./Copyright'));


function Home(props) {

 // navigations-----------------------------------------------------------------
  const {goHome, goAbout, goEducation, goSkills, goProjects, goContact} = Navigationcode();


	{/*for web developer ---------------------------------------------*/}

  const[developerIndex,setDeveloperIndex]=useState(0);
  const developer=["Web","Front-end"];
  useEffect(()=>{
  	const interval=setInterval(()=>{
  		setDeveloperIndex((prevIndex)=>(prevIndex+1) % developer.length);
  	},1500);
  	return ()=>clearInterval(interval);
  },[]);


	{/*main ontent ---------------------------------------------*/}

  return (
    <>
    <Navbar/>

    <div className="container-fluid container-fluid-nooverflow home-container">
      <div className="row justify-content-start align-items-center home-box"> 
        <div className="col-10 col-sm-10 col-md-5 col-lg-5 col-xl-5 col-xxl-5">
          <div className="home-image-box">
            <img className="my-photo" src={myPhoto3} alt="myPhoto" loading="lazy"/>
          </div>
        </div>
        <div className="col-10 col-sm-10 col-md-6 col-lg-6 col-xl-6 col-xxl-6 home-name-box">
          <h2 className="home-hi-text">Hey, I'm </h2>
          <h1 className="home-name-text ">{props.name}</h1>
          <h3 className="home-dev-text ">  [ {developer[developerIndex]} Developer ] </h3>
          <button onClick={goAbout}>About Me</button>
          <button onClick={goProjects}>My Projects</button> <br/>
          <a href="https://www.facebook.com/profile.php?id=100009095791567" target="_blank" rel="noopener noreferrer" className="nav-social-icon " title="Connect! Mukesh Kumar
          ">
            <FontAwesomeIcon icon={faFacebook} />
          </a>
          <a href="https://www.instagram.com/im_mukesh_9/" target="_blank" rel="noopener noreferrer" className="nav-social-icon" title="Follow! @im_mukesh_9">
            <FontAwesomeIcon icon={faInstagram} />
          </a>
          <a href="https://github.com/MukeshGit9" target="_blank" rel="noopener noreferrer" className="nav-social-icon" title="MukeshGit9">
            <FontAwesomeIcon icon={faGithub} />
          </a>
          {/*<a href="https://www.youtube.com/@TechnoXpress" target="_blank" rel="noopener noreferrer" className="nav-social-icon" title="Subscribe! @TechnoXpress">
            <FontAwesomeIcon icon={faYoutube} />
          </a>*/}
          
        </div>
      </div>
    </div>

    <Copyright/>
    </>
  );
}

export default Home;
