import {useNavigate, useLocation} from 'react-router-dom';

function Navigationcode(){
			const navigatie = useNavigate();
			const goHome = ()=>{
				window.scrollTo({top:0, behavior:'smooth'});
				navigatie('/');
			};

			const goAbout=()=>{
				window.scrollTo({top:0, behavior:'smooth'});
				navigatie('/about');
			};

			const goEducation=()=>{
				window.scrollTo({top:0, behavior:'smooth'});
				navigatie('/education');
			};

			const goSkills=()=>{
				window.scrollTo({top:0, behavior:'smooth'});
				navigatie('/skills');
			};

			const goProjects=()=>{
				window.scrollTo({top:0, behavior:'smooth'});
				navigatie('/projects');
			};

			const goContact=()=>{
				window.scrollTo({top:0, behavior:'smooth'});
				navigatie('/contact');
			};

	return{
			goHome, goAbout, goEducation, goSkills, goProjects, goContact
	
			};
}

export default Navigationcode;